%normalText {
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  font-weight: 500;
  line-height: 100%;
}

%flexConfigSpaceBetween {
  display: flex;
  justify-content: space-between;
}

%flexConfigSpaceAround {
  display: flex;
  justify-content: space-around;
}

%flexConfigCenter {
  display: flex;
  justify-content: center;
}
